	var SectionItemView = Backbone.View.extend(
	{
		tagName: "li",
		model: Cart.Item,

		events: {
			'click .cp-item-trash': 'trash'
		},

		initialize: function () {
			this.listenTo(this.model, 'destroy', this.remove);
			this.listenTo(this.model, 'change:quantity', this.render);
		},

		trash: function ()
		{
			this.model.destroy();
		}
	});



	//┐
	//│  ┌──────────────────────────────────────────────────────────────────────────────────────┐
	//╠──┤  RENDERING                                                                           │
	//│  └──────────────────────────────────────────────────────────────────────────────────────┘
	//┘

		_.extend(SectionItemView.prototype,
		{
			/**
			 *	Рендер представления
			 */

			render: function ()
			{
				if (this.model.changed.id !== undefined) {
					return;
				}

				this.$el.html(ItemHTML(this.model.toJSON()));

				return this;
			}
		});


module.exports = SectionItemView;






