


/** 
 *	Require third-party library
 */

var _ = require('underscore'),
	Backbone = require('backbone'),
	IScroll = require('iscroll');


/** 
 *	Require base
 */
 
var	Lifecycle = require('../../../../base/Lifecycle'),
	LifecycleEvent = require('../../../../base/LifecycleEvent');


/** 
 *	Require component
 */
 
var SectionItemView = require('./SectionItemView');




//┐
//│  ╔══════════════════════════════════════════════════════════════════════════════════════════╗
//│  ║                                                                                          ║
//╠──╢  СЕКЦИЯ ПАНЕЛИ                                                                           ║
//│  ║                                                                                          ║
//│  ╚══════════════════════════════════════════════════════════════════════════════════════════╝
//┘

	var SectionView = Backbone.View.extend(
	{
		initialize: function (options)
		{
			this.list = options.list;
			//this.listenTo(this.list, 'add', this.insertItem);
			//this.listenTo(this.list, 'reset', this.insertItems);
			this.$list = this.$el.find('.cp-section-list');
			//console.log();
			

			this.scroller = new IScroll('.' + this.el.className + ' .cp-section-wrapper', {
				mouseWheel: true,
				scrollbars: true,
				fadeScrollbars: true,
				interactiveScrollbars: true,
				resizeScrollbars: true,
				shrinkScrollbars: true,
				bounce: true,
				directionLockThreshold: 5,
				bounceTime: 600,
				snapThreshold: 0.334,
				click: true,
				deceleration: 0.0006,
				probeType: 3
			});

			this.listenTo(Lifecycle, LifecycleEvent.ENTER, _.bind(function(){
				this.scroller.refresh();
			}, this));
			
			this.deactivate();
		},

		activate: function ()
		{
			console.log('activate');
			this.insertItems();
			this.delegateEvents(this.events);
			setTimeout(_.bind(function () {
				this.scroller.refresh();
			}, this), 300);
		},

		deactivate: function ()
		{
			this.undelegateEvents();
			//this.scroller.disable();
		},

		insertItem: function (item)
		{
			var view = new SectionItemView({ model: item });
			this.$list.append(view.render().el);
		},

		insertItems: function ()
		{
			this.$list.html('');
			this.list.each(this.insertItem, this);
			
		}
	});



	//┐
	//│  ┌──────────────────────────────────────────────────────────────────────────────────────┐
	//╠──┤  RENDERING                                                                           │
	//│  └──────────────────────────────────────────────────────────────────────────────────────┘
	//┘

		_.extend(SectionView.prototype,
		{
			/**
			 *	Рендер представления
			 */

			render: function ()
			{
				//
			}
		});


module.exports = SectionView;






