	var Lifecycle = function ()
	{
		this._state = '';
	};



	//┐
	//│  ┌──────────────────────────────────────────────────────────────────────────────────────┐
	//╠──┤  API LIFECYCLE                                                                       │
	//│  └──────────────────────────────────────────────────────────────────────────────────────┘
	//┘

		_.extend(Lifecycle.prototype, Backbone.Events,
		{

			/** 
			 *	Меняет состояние
			 */

			next: function (state)
			{
				var a = Array.prototype.slice.call(arguments);

				if (_.isString(state)) {
					this._state = state;
					this.trigger.apply(this, a);
				}
			},

			
			/** 
			 *	Возвращает текущее состояние
			 */

			current: function ()
			{
				return this._state;
			}

		});


module.exports = new Lifecycle();






