


/** 
 *	Require third-party library
 */

var Backbone = require('backbone');


/** 
 *	Require base
 */
 
var	Cart = require('../../../base/Cart'),
	Lifecycle = require('../../../base/Lifecycle'),
	LifecycleEvent = require('../../../base/LifecycleEvent'),
	Utils = require('../../../base/Utils');


/** 
 *	Require component
 */
 
var Options = require('../TemplateOptions'),
	SectionView = require('./panel/SectionView');


/** 
 *	Require HTML template
 */
 
var	HeaderHTML = require('../html/panel/header.html');
var ContentHTML = require('../html/panel/content.html');
var FooterHTML = require('../html/panel/footer.html');




//┐
//│  ╔══════════════════════════════════════════════════════════════════════════════════════════╗
//│  ║                                                                                          ║
//╠──╢  ПАНЕЛЬ                                                                                  ║
//│  ║                                                                                          ║
//│  ╚══════════════════════════════════════════════════════════════════════════════════════════╝
//┘

	var PanelView = Backbone.View.extend(
	{

		/** Interaction events */

		events: {
			'click .cp-back' : 'hidePanel',
			'click .cart-tab' : 'selectCartSection',
			'click .wish-tab' : 'selectWishSection'
		},


		initialize: function ()
		{
			this.iswish = Options.vars.iswish;
			this.currency = Options.vars.currency;

			this.cartList = Cart.getList('cartlist');
			this.wishList = Cart.getList('wishlist');

			this.section = 'cart';


			this.$el.append(HeaderHTML({ iswish: this.iswish }));
			//this.$el.append(ContentHTML({ iswish: this.iswish }));
			this.$el.append(FooterHTML());


			this.$headerTotalCost = $('.cart-tab .cp-tab-label');
			this.$headerTotalWish = this.iswish ? $('.wish-tab .cp-tab-label') : null;


			//this.cartSection = new SectionView({ el: '.cp-section-cart', list: this.cartList });
			//this.wishSection = this.iswish ? new SectionView({ el: '.cp-section-wish', list: this.wishList }) : null;

		},



		/** 
		 *	Меняет секцию
		 *	инициирует: this
		 */

		change: function (section)
		{
			if (this.section == section) return;
			else this.section = section;


			this.$el.toggleClass('cp-section_cart', this.section == 'cart');
			this.iswish && this.$el.toggleClass('cp-section_wish', this.section == 'wish');


			//this.section == 'cart' ? this.cartSection.activate() : this.cartSection.deactivate();
			//this.iswish ? this.section == 'wish' ? this.wishSection.activate() : this.cartSection.deactivate() : null;


			/*TweenMax.to($('.cp-tab-selector'), 0.3, { 
				xPercent: (this.section == 'wish') ? 100 : 0, 
				ease: Power1.easeOut, 
				onComplete: _.bind(this._changed, this) 
			});*/

			/*TweenMax.to($('.cp-container > ul'), 0.3, {
				xPercent: (this.section == 'wish') ? -50 : 0, 
				ease: Power1.easeOut
			});*/

		},

		/** 
		 *	Оповещает о том, что текущая секция поменялась
		 *	инициирует: this
		 */

		_changed: function ()
		{
			//
		}
		
	});



	//┐
	//│  ┌──────────────────────────────────────────────────────────────────────────────────────┐
	//╠──┤  INTERACTION                                                                         │
	//│  └──────────────────────────────────────────────────────────────────────────────────────┘
	//┘

		_.extend(PanelView.prototype,
		{
			/**
			 *	Скрывает панель
			 */

			hidePanel: function ()
			{
				Lifecycle.next(LifecycleEvent.HIDE);
			},


			/**
			 *	Выбирает таб корзины
			 */

			selectCartSection: function ()
			{
				this.change('cart');
			},


			/**
			 *	Выбирает таб отложенных товаров
			 */

			selectWishSection: function ()
			{
				this.change('wish');
			},


			/**
			 *	Активирует взаимодействие
			 */

			activateInteraction: function ()
			{
				this.delegateEvents(this.events);
			},


			/**
			 *	Деактивирует взаимодействие
			 */

			deactivateInteraction: function ()
			{
				this.undelegateEvents();
			},
		});



	//┐
	//│  ┌──────────────────────────────────────────────────────────────────────────────────────┐
	//╠──┤  RENDERING                                                                           │
	//│  └──────────────────────────────────────────────────────────────────────────────────────┘
	//┘

		_.extend(PanelView.prototype,
		{
			/**
			 *	Активирует рендер
			 */

			activateRendering: function (section)
			{
				this.change(section);

				this.listenTo(this.cartList, 'destroy change', this.render);
				this.iswish && this.listenTo(this.wishList, 'destroy change', this.render);
				this.render();
			},


			/**
			 *	Деактивирует рендер
			 */

			deactivateRendering: function ()
			{
				this.stopListening(this.cartList);
				this.iswish && this.stopListening(this.wishList);
			},


			/**
			 *	Рендер представления
			 */

			render: function ()
			{
				var cartListStats = _.clone(this.cartList.getStats()),
					wishListStats = this.iswish ? _.clone(this.wishList.getStats()) : null;

				this.$headerTotalCost.html(Utils.format(cartListStats.totalCost, this.currency));
				this.$headerTotalWish && this.$headerTotalWish.html(wishListStats ? wishListStats.totalItems : 0);
			}
		});


module.exports = PanelView;






