


/** 
 *	Currency
 */
 
var	currencyFormat = {
	'usd': { before: '$', after: '', divider: ',', n: 2, x: 3 },
	'rub': { before: '', after: ' руб.', divider: ' ', n: 0, x: 3 }
};




